import * as React from 'react';
import { PartialMemberData } from 'Pages/CustomerCarePage/Models';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { API } from 'Pages/CustomerCarePage/API';

interface IProps {
    searchText: string;
    onSelectMember: (cardNo: string) => void;
}

export class SearchedMembersList extends React.Component<IProps> {

    members: PartialMemberData[];

    constructor(props: IProps) {
        super(props);

        this.members = [];
    }

    componentDidMount() {
        API.get('customer-care/members', 
            (response) => {
                console.log(response);
                this.members = response.map(member => {
                    return {
                        cardNo: member.member_card_no,
                        firstName: member.first_name,
                        middleName: member.middle_name,
                        lastName: member.last_name,
                        clientName: member.client.registered_name
                    }
                });
                console.log(this.members);
            },
            (error) => {
                console.log(error);
            });
    }

    getExtractedMembers = (filter: string) => {
        return (filter.trim().length > 0 ? (
            this.members
                .filter(member => member.cardNo.includes(filter))
                .filter((_member, index) => index < 3)
        ) : []);
    }

    render(): JSX.Element {
        return (
            <div className='mini-ticket-modals'>
                {this.getExtractedMembers(this.props.searchText).map(member => {
                    return (
                        <>
                        {this.renderMemberShortDetail(member)}
                        <div className='line-border' />
                        </>
                    );
                })}
            </div>
        );
    }

    renderMemberShortDetail(member: PartialMemberData): JSX.Element {
        const name = 
            member.firstName + ' ' + 
            GlobalFunction.getIntials(member.middleName) + ' ' +
            member.lastName;

        return (
            <div 
                className='small-section mini-ticket-modal'
                onClick={() => this.props.onSelectMember(member.cardNo)}>
                <div className='text' />
                <div className='text'>
                    <div className='circle' style={{backgroundColor: '#3AB77D'}} />
                    <div style={{display: 'inline-block'}}>
                        <div className='header-2'>{member.cardNo}</div>
                    </div>

                </div>
                
                <div className='text'>{name}</div>
                <div className='text'>{member.clientName}</div>
                <div className='text'>{'---'}</div>
            </div>
        );
    }
}