import * as React from 'react';
import { Grid, TableCell, TableRow, Tabs, Tab } from '@material-ui/core';
import { ConfinementDetails } from '../ResolveTicketPage';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { DiagnosisData, ProcedureData, DoctorData } from 'Pages/CustomerCarePage/Models';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { AccommodationData } from 'Pages/CustomerCarePage/UpdateTicketPage/UpdateTicketProps';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';
import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';

interface IProps {
    confinement: ConfinementDetails;
    refreshConfinementState: () => void;
}

interface IStates {
    selectedTab: any;
}


export class ApproveInpatientSection extends React.Component<IProps, IStates> {

    APPROVED = 'Approved';
    DISAPPROVED = 'Disapproved';
    PENDING = 'Pending';

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 0
        }
    }

    getTotalAccommodationCost = () => {
        let totalCost = 0;
        this.props.confinement.accommodations.map(accommodation => {
            totalCost += accommodation.costPerDay * accommodation.days;
        });
        return totalCost;
    }

    getProceduresCost = (diagnosis: DiagnosisData) => {
        let total = 0;
        diagnosis.procedures.map(procedure => {
            total += procedure.amount;
        });
        return total;
    }

    getAttendingPhysicianCost = (diagnosis: DiagnosisData) => {
        let total = 0;
        diagnosis.doctors
            .filter(doctor => doctor.type === 'Attending')
            .map(doctor => {
                total += doctor.fee;
            });
        return total;
    }

    getAllProceduresCost = () => {
        let total = 0;
        this.props.confinement.diagnoses.map(diagnosis => {
            total += this.getProceduresCost(diagnosis) + 
                     this.getAttendingPhysicianCost(diagnosis) +
                     this.getTotalAccommodationCost();
        });
        return total;
    }

    getAllProceduresCount = () => {
        let total = 0;
        this.props.confinement.diagnoses.map(diagnosis => {
            total += diagnosis.procedures.length;
        });
        return total;
    }

    render(): JSX.Element {
        const confinement = this.props.confinement;
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Approve or Disapprove Inpatient</div>
                <Grid container spacing={2}>
                    <Grid item xs={4}>
                        <div className='sub-section-2'>
                            <div className='light-text'>PROVIDER</div>
                            <div className='small-text'>
                                {`${confinement.provider.value} - ${confinement.provider.label}`}
                            </div>
                        </div>
                        <div className='sub-section-2'>
                            <div className='light-text'>KIND OF CONFINEMENT</div>
                            <div className='small-text'>{confinement.confinementKind}</div>
                        </div>
                    </Grid>
                    <Grid item xs={4}>
                        <div className='sub-section-2'>
                            <div className='light-text'>DATE ADMITTED</div>
                            <div className='small-text'>
                                {GlobalFunction.toCommonDateString(confinement.dateAdmitted)}
                            </div>
                        </div>
                        <div className='sub-section-2'>
                            <div className='light-text'>PHILHEALTH</div>
                            <div className='small-text'>{confinement.coverage.label}</div>
                        </div>
                    </Grid>
                    <Grid item xs={4}>
                        <div className='sub-section-2'>
                            <div className='light-text'>LINKED TICKETS</div>
                            <div className='small-text'>{'---*'}</div>
                        </div>
                        <div className='sub-section-2'>
                            <div className='light-text'>GIFT GIVEN</div>
                            <div className='small-text'>{confinement.giftGiven ? 'Yes' : 'No'}</div>
                        </div>
                    </Grid>
                </Grid>
                {this.renderTabs()}
                {this.state.selectedTab === 0 ? (
                    this.renderSummarySection(confinement)
                ) : (
                    this.renderDiagnosisSection(
                        confinement.diagnoses.filter(diagnosis => 
                            diagnosis.icd10Code.value === this.state.selectedTab
                        )[0]
                    )
                )}
                
                <div className='section' />
            </div>
        );
    }

    renderTabs(): JSX.Element {
        return (
            <Tabs
                className='custom-tabs sub-section' 
                value={this.state.selectedTab}
                variant='scrollable'
                scrollButtons='auto'
                indicatorColor='primary'
                onChange={(_event: React.ChangeEvent<{}>, value: number) => {
                    this.setState({selectedTab: value});
                }}>
                    <Tab 
                        value={0} 
                        label={
                            <div>
                                <div className='header-2'>SUMMARY OF DIAGNOSIS</div>
                                <div className='smaller-text'>
                                    {`${GlobalFunction.formatNumber(this.getAllProceduresCost())}.00 PHP 
                                    (${this.getAllProceduresCount()} Procedures)`}
                                </div>
                            </div>
                        } />
                    {this.props.confinement.diagnoses
                        .filter(diagnosis => diagnosis.isPrimary)
                        .map(diagnosis => {
                            return (
                                <Tab 
                                    value={diagnosis.icd10Code.value} 
                                    label={
                                        <div>
                                            <div className='header-2'>PRIMARY DIAGNOSIS</div>
                                            <div className='smaller-text'>
                                                {`${GlobalFunction.formatNumber(this.getProceduresCost(diagnosis))}.00 PHP 
                                                (${diagnosis.procedures.length} Procedures)`}
                                            </div>
                                        </div>
                                    } />
                            );
                        })}
                    {this.props.confinement.diagnoses
                        .filter(diagnosis => !diagnosis.isPrimary)
                        .map((diagnosis, index) => {
                            const totalCost = 
                                this.getProceduresCost(diagnosis) +
                                this.getTotalAccommodationCost() +
                                this.getAttendingPhysicianCost(diagnosis);
                            return (
                                <Tab 
                                    value={diagnosis.icd10Code.value} 
                                    label={
                                        <div>
                                        <div className='header-2'>{`DIAGNOSIS ${index + 1}`}</div>
                                        <div className='smaller-text'>
                                            {`${GlobalFunction.formatNumber(totalCost)}.00 PHP 
                                            (${diagnosis.procedures.length} Procedures)`}
                                        </div>
                                        </div>
                                    } />
                            );
                        })}
            </Tabs>
        );
    }

    renderSummarySection(confinement: ConfinementDetails): JSX.Element {
        return (
            <div>
                {confinement.diagnoses.map((diagnosis, index) => {
                    return this.renderDiagnosisInfo(diagnosis, index);
                })}
            </div>
        );
    }

    renderDiagnosisInfo(diagnosis: DiagnosisData, index: number): JSX.Element {
        const procedureCost = this.getProceduresCost(diagnosis);
        const accommodationCost = this.getTotalAccommodationCost();
        const attendingPhysicianCost = this.getAttendingPhysicianCost(diagnosis);
        return (
            <Grid container className='sub-section'>
                <div style={{width: '2rem'}}>
                    <div className='number-item'>{index + 1}</div>
                </div>
                <div style={{width: 'calc(100% - 2rem)'}}>
                    <div style={{marginBottom: '1rem'}}>
                        <div className='text'>
                            {`${diagnosis.icd10Code.value} — ${diagnosis.icd10Code.label}`}
                            {diagnosis.isPrimary ? (
                                <span className='header-2'>{' (PRIMARY)'}</span>
                            ) : null}
                        </div>
                        {diagnosis.doctors.filter(doctor => doctor.type === 'Admitting')
                            .map(doctor => {
                                return (
                                    <>
                                    <div className='small-text'>{`${doctor.id} - ${doctor.name}`}</div>
                                    <div className='small-text'>
                                        {`PHP ${GlobalFunction.formatNumber(doctor.fee)}.00`}
                                    </div>
                                    </>
                                );
                            })
                        }
                        
                        <div className='small-text'>
                            {`Remarks: ${diagnosis.remarks ? diagnosis.remarks : 'None'}`}
                        </div>
                        <div className='small-text'>
                            {`PHIC: No*`}
                        </div>
                        <div className='header-2'>
                            {`TOTAL AMOUNT: ${GlobalFunction.formatNumber(
                                procedureCost + accommodationCost + attendingPhysicianCost
                            )}.00 PHP`}
                        </div>
                        <TextButton 
                            label='Approve/Disapprove Diagnosis' 
                            onClick={() => {
                                window.scroll(0, -window.pageYOffset);
                                this.setState({selectedTab: diagnosis.icd10Code.value});
                            }} />
                    </div>

                    {this.renderTableSection('PROCEDURES',
                        <CustomTable
                            className='custom-table'
                            rows={diagnosis.procedures}
                            renderHeader={() => {
                                return (
                                    <>
                                    <TableCell>Procedure</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '150px'}}>Amount</TableCell>
                                    <TableCell style={{width: '150px'}}>Date</TableCell>
                                    <TableCell style={{width: '250px'}}>Clause</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '150px'}}>Remaining Bal.</TableCell>
                                    </>
                                );
                            }}
                            renderRow={(procedure: ProcedureData, index: number) => {
                                const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                                return (
                                    <TableRow style={{backgroundColor: backgroundColor}}>
                                        <TableCell>{`${procedure.cptCode.value} — ${procedure.cptCode.label}`}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>
                                            {`${GlobalFunction.formatNumber(procedure.amount)}.00 PHP`}
                                        </TableCell>
                                        <TableCell>{GlobalFunction.toShortDateString(procedure.dateAdded)}</TableCell>
                                        <TableCell>{`This is the clause*`}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(9999)}.00 PHP*`}</TableCell>
                                    </TableRow>
                                );
                            }} />,
                            procedureCost
                    )}

                    {this.renderTableSection('ROOM ACCOMMODATIONS',
                        <CustomTable
                            className='custom-table'
                            rows={this.props.confinement.accommodations}
                            renderHeader={() => {
                                return (
                                    <>
                                    <TableCell>Room Type</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '200px'}}>No. of days</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '200px'}}>Cost per day</TableCell>
                                    <TableCell style={{textAlign: 'center', width: '200px'}}>HIMS Assessment</TableCell>
                                    </>
                                );
                            }}
                            renderRow={(accommodations: AccommodationData, index: number) => {
                                const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                                return (
                                    <TableRow style={{backgroundColor: backgroundColor}}>
                                        <TableCell>{accommodations.roomType}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{accommodations.days}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(accommodations.costPerDay)}.00 PHP`}</TableCell>
                                        <TableCell style={{textAlign: 'center'}}>{`Approve*`}</TableCell>
                                    </TableRow>
                                );
                            }} />,
                            accommodationCost
                    )}

                    {this.renderTableSection('ATTENDING PHYSICIANS',
                        <CustomTable
                            className='custom-table'
                            rows={diagnosis.doctors.filter(doctor => doctor.type === 'Attending')}
                            renderHeader={() => {
                                return (
                                    <>
                                    <TableCell>Name of Doctor</TableCell>
                                    <TableCell style={{width: '200px'}}>Specialization</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '125px'}}>No. of Visits</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '200px'}}>Amount</TableCell>
                                    </>
                                );
                            }}
                            renderRow={(doctor: DoctorData, index: number) => {
                                const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                                return (
                                    <TableRow style={{backgroundColor: backgroundColor}}>
                                        <TableCell>{doctor.name}</TableCell>
                                        <TableCell>{doctor.specialization}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{doctor.noOfVisits}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(doctor.fee)}.00 PHP`}</TableCell>
                                    </TableRow>
                                );
                            }} />,
                            attendingPhysicianCost
                    )}
                </div>
            </Grid>
        );
    }

    renderTableSection(headerName: string, table: JSX.Element, totalValue?: number): JSX.Element {
        return (
            <div className='sub-section'>
                <div className='header-2'>{headerName}</div>
                {table}
                {totalValue ? (
                    <div className='sub-section'>
                        <div style={{float: 'right'}}>
                            <span className='small-text'>TOTAL AMOUNT</span>
                            <span style={{marginLeft: '1rem'}} />
                            <span className='header-2'>
                                {GlobalFunction.formatNumber(totalValue) + '.00 PHP'}
                            </span>
                        </div>
                </div>
                ) : null}
            </div>
        );
    }

    renderDiagnosisSection(diagnosis: DiagnosisData): JSX.Element {
        const procedureCost = this.getProceduresCost(diagnosis);
        const accommodationCost = this.getTotalAccommodationCost();
        const attendingPhysicianCost = this.getAttendingPhysicianCost(diagnosis);
        return (
            <div className='sub-section' style={{marginLeft: '1rem'}}>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <div className='text'>
                            {`${diagnosis.icd10Code.value} — ${diagnosis.icd10Code.label}`}
                            {diagnosis.isPrimary ? (
                                <span className='header-2'>{' (PRIMARY)'}</span>
                            ) : null}
                        </div>
                        {diagnosis.doctors.filter(doctor => doctor.type === 'Admitting')
                            .map(doctor => {
                                return (
                                    <>
                                    <div className='small-text'>{`${doctor.id} - ${doctor.name}`}</div>
                                    <div className='small-text'>
                                        {`PHP ${GlobalFunction.formatNumber(doctor.fee)}.00`}
                                    </div>
                                    </>
                                );
                            })
                        }
                        
                        <div className='small-text'>
                            {`Remarks: ${diagnosis.remarks ? diagnosis.remarks : 'None'}`}
                        </div>
                        <div className='small-text'>
                            {`PHIC: No*`}
                        </div>
                        <div className='header-2'>
                            {`TOTAL AMOUNT: ${GlobalFunction.formatNumber(
                                procedureCost + accommodationCost + attendingPhysicianCost
                            )}.00 PHP`}
                        </div>
                    </Grid>
                    {/* <Grid item xs={2}>
                        <div className='light-text'>Choose Assessment</div>
                        <CustomRadio 
                            label='Approve'
                            // checkedCondition={procedure.assessmentType === this.APPROVED}
                            checkedCondition={false}
                            onChange={() => {
                                // procedure.assessmentType = this.APPROVED;
                                // this.props.refreshConfinementState();
                            }} />
                        <CustomRadio 
                            label='Disapprove'
                            // checkedCondition={procedure.assessmentType === this.DISAPPROVED}
                            checkedCondition={false}
                            onChange={() => {
                                // procedure.assessmentType = this.DISAPPROVED;
                                // this.props.refreshConfinementState();
                            }} />
                    </Grid>
                    <Grid item xs={4}>
                        <div className='light-text'>Remarks</div>
                        <InputArea
                            value={''}
                            onChange={(_value: string) => {
                                // procedure.assessmentRemarks = value;
                                this.props.refreshConfinementState();
                            }} />
                    </Grid> */}
                    
                </Grid>
                <div className='sub-section'>
                    <ComplexTable 
                        data={diagnosis.procedures}
                        renderHeader={() => this.renderHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderRow(rowData, index)} />
                </div>

                {this.renderTableSection('ROOM ACCOMMODATIONS',
                        <CustomTable
                            className='custom-table'
                            rows={this.props.confinement.accommodations}
                            renderHeader={() => {
                                return (
                                    <>
                                    <TableCell>Room Type</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '200px'}}>No. of days</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '200px'}}>Cost per day</TableCell>
                                    <TableCell style={{textAlign: 'center', width: '200px'}}>HIMS Assessment</TableCell>
                                    </>
                                );
                            }}
                            renderRow={(accommodations: AccommodationData, index: number) => {
                                const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                                return (
                                    <TableRow style={{backgroundColor: backgroundColor}}>
                                        <TableCell>{accommodations.roomType}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{accommodations.days}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(accommodations.costPerDay)}.00 PHP`}</TableCell>
                                        <TableCell style={{textAlign: 'center'}}>{`Approve*`}</TableCell>
                                    </TableRow>
                                );
                            }} />,
                            accommodationCost
                    )}

                    {this.renderTableSection('ATTENDING PHYSICIANS',
                        <CustomTable
                            className='custom-table'
                            rows={diagnosis.doctors.filter(doctor => doctor.type === 'Attending')}
                            renderHeader={() => {
                                return (
                                    <>
                                    <TableCell>Name of Doctor</TableCell>
                                    <TableCell style={{width: '200px'}}>Specialization</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '125px'}}>No. of Visits</TableCell>
                                    <TableCell style={{textAlign: 'right', width: '200px'}}>Amount</TableCell>
                                    </>
                                );
                            }}
                            renderRow={(doctor: DoctorData, index: number) => {
                                const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                                return (
                                    <TableRow style={{backgroundColor: backgroundColor}}>
                                        <TableCell>{doctor.name}</TableCell>
                                        <TableCell>{doctor.specialization}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{doctor.noOfVisits}</TableCell>
                                        <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(doctor.fee)}.00 PHP`}</TableCell>
                                    </TableRow>
                                );
                            }} />,
                            attendingPhysicianCost
                    )}
                <div className='section' />
            </div>
        );
    }













    renderHeader(): JSX.Element {
        const headerNameStyle = {
            margin: 0,
            padding: '12px 0 12px 0'
        };

        return (
            <>
                <Grid item xs={4}>
                    <div className='header-2' style={headerNameStyle}>Procedure Details</div>
                </Grid>
                <Grid item xs={4}>
                    <div className='header-2' style={headerNameStyle}>System Details</div>
                </Grid>
                <Grid item xs={4}>
                    <div className='header-2' style={headerNameStyle}>Assessment</div>
                </Grid>
            </>
        );
    }

    renderRow(procedure: ProcedureData, index: number): JSX.Element {
        return (
            <div className='section' >
                
                <Grid container>
                    <Grid item xs={4}>
                        <div>
                            <Grid container>
                                <Grid item xs={2} style={{padding: '0 0.5rem', marginTop: '-3px'}}>
                                    <div className='number-item'>{index + 1}</div>
                                </Grid>
                                <Grid item xs={10} className='header-2'>
                                    {procedure.cptCode.value + ' - ' + procedure.cptCode.label}
                                </Grid>
                            </Grid>
                        </div>
                    </Grid>
                    
                </Grid>
                <Grid container>
                    <Grid item xs={4}>
                        <div>
                            <Grid container>
                                <Grid item xs={2} />
                                <Grid item xs={10}>
                                    <div className='sub-section-2'>
                                        <div className='light-text'>AMOUNT</div>
                                        <div className='small-text'>{'PHP ' + GlobalFunction.formatNumber(procedure.amount) + '.00'}</div>
                                    </div>
                                    <div className='sub-section-2'>
                                        <div className='light-text'>PHILHEALTH REQUIRED?</div>
                                        <div className='small-text'>{procedure.isPHIC ? 'Yes' : 'No'}</div>
                                    </div>
                                    <div className='sub-section-2'>
                                        <div className='light-text'>DATE OF AVAILMENT</div>
                                        <div className='small-text'>{'May 19, 2019 *'}</div>
                                    </div>
                                </Grid>
                            </Grid>
                        </div>
                    </Grid>
                    <Grid item xs={4}>
                        <div className='sub-section-2'>
                            <div className='light-text'>SYSTEM ASSESSMENT</div>
                            <div className='small-text'>{'Approve *'}</div>
                        </div>
                        <div className='sub-section-2'>
                            <div className='light-text'>REMAINING BALANCE</div>
                            <div className='small-text'>{'PHP ' + 0 + '.00 *'}</div>
                        </div>
                        <div className='sub-section-2'>
                            <div className='light-text'>CLAUSE</div>
                            <div className='small-text'>{'Laboratory tests, x-rays, and other diagnostic examinations prescribed by the INTELLICARE physician*'}</div>
                        </div>
                    </Grid>
                    <Grid item xs={2}>
                        <div className='sub-section-2'>
                            <div className='light-text'>Choose Assessment</div>
                            <CustomRadio 
                                label='Approve'
                                checkedCondition={procedure.assessmentType === this.APPROVED}
                                onChange={() => {
                                    procedure.assessmentType = this.APPROVED;
                                    this.props.refreshConfinementState();
                                }} />
                            <CustomRadio 
                                label='Disapprove'
                                checkedCondition={procedure.assessmentType === this.DISAPPROVED}
                                onChange={() => {
                                    procedure.assessmentType = this.DISAPPROVED;
                                    this.props.refreshConfinementState();
                                }} />
                            <CustomRadio 
                                label='Pending'
                                checkedCondition={procedure.assessmentType === this.PENDING}
                                onChange={() => {
                                    procedure.assessmentType = this.PENDING;
                                    this.props.refreshConfinementState();
                                }} />
                        </div>
                    </Grid>
                    <Grid item xs={2}>
                        <div className='sub-section-2'>
                            <div className='light-text'>Remarks</div>
                            <InputArea
                                value={procedure.assessmentRemarks}
                                onChange={(value: string) => {
                                    procedure.assessmentRemarks = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </div>
                    </Grid>
                </Grid>
            </div>
        );
    }
}