import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { GlobalFunction } from './GlobalFunction';
import { MemberData, TicketData, PartialMemberData, SimpleTicketData } from 'Pages/CustomerCarePage/Models';
import { TextButton } from './Components/TextButton';
import { SearchedMembersList } from './CreateTicketPage/Components/SearchedMembersList';
import { API } from './API';
import { TableRow, TableCell } from '@material-ui/core';
import { Loading } from './Components/Loading';

interface IProps {
    member?: MemberData;
    ticket?: TicketData;
    ticketStatus: 'Create' | 'Update' | 'Resolve';
    onUpdateMemberStateChange: (member?: MemberData) => void;

    promptChooseMember?: boolean;
}

interface IStates {
    searchText: string;
    isMembersLoaded: boolean;
    recentTickets?: SimpleTicketData[];
    loadingMember: boolean;
}

export class MemberDataSection extends React.Component<IProps, IStates> {

    members: PartialMemberData[];
    

    constructor(props: IProps) {
        super(props);

        this.state = {
            searchText: '',
            isMembersLoaded: false,
            loadingMember: false
        };

        this.members = [];
    }

    // shouldComponentUpdate(props: IProps, state: IStates) {
    //     return (
    //         (this.props.member && props.member? props.member.cardNo !== this.props.member.cardNo : false) ||
    //         (this.state.searchText !== state.searchText) ||
    //         (this.state.loadingMember !== state.loadingMember) ||
    //         (this.state.recentTickets ? this.state.recentTickets !== state.recentTickets : false)
    //     );
    // }

    getMemberData = (cardNo: string) => {
        API.get('customer-care/members/' + cardNo, 
            (response) => {
                console.log(response);
                const member = {
                    cardNo: response.member_card_no,
                    firstName: response.first_name,
                    middleName: response.middle_name,
                    lastName: response.last_name,
                    clientName: response.client.registered_name,
                    birthDate: new Date(response.date_of_birth),
                    gender: response.gender,
                    civilStatus: response.civil_status
                };
                this.props.onUpdateMemberStateChange(member);
                console.log(this.members);

                /* Get member's benefits */
                API.get('customer-care/members/' + cardNo + '/benefits',
                    (response) => {
                        console.log(response);
                    },
                    (error) => {
                        console.log(error);
                    });

                /* Get Recent Tickets */
                this.getRecentTicketsData(cardNo);
                this.setState({loadingMember: false});
                
            },
            (error) => {
                console.log(error);
            });
    }

    getRecentTicketsData = (cardNo: string) => {
        API.get('customer-care/tickets',
            (response) => {
                console.log(response);
                this.setState({recentTickets: response
                    .filter(ticket => ticket.member_id === cardNo)
                    .map(ticket => {
                        return {
                            ticketNo: ticket.ticket_no,
                            specificType: ticket.ticket_category.specific_type,
                            lastUpdate: new Date(ticket.date_updated)
                        }
                    })
                });
            },
            (error) => {
                console.log(error);
            });
    }










    render(): JSX.Element {
        return (
            <div className='member-info'>
                {this.props.ticketStatus === 'Create' ? (
                    <div>
                        {this.renderSearchArea()}
                        <div className='line-border' />
                    </div>
                ) : this.props.ticketStatus === 'Update' ||
                    this.props.ticketStatus === 'Resolve' ? (
                    this.renderTopArea()
                ) : (null)}
                {this.props.member ? (
                    this.renderMemberInfo(this.props.member)
                ) : (
                    <>
                    {this.state.loadingMember ? (
                        <Loading height='4rem' />
                    ) : (null)}
                    {this.props.promptChooseMember ? (
                        <div className='section' style={{color: '#E53935'}}>
                            Choose a member/client first before submitting the ticket.
                        </div> 
                    ) : (null)}
                    </>
                )}
                    
                {this.props.ticket ? (
                    this.renderTicketData(this.props.ticket)
                ) : (null)}
                <div className='section' />
            </div>
        );
    }

    renderSearchArea(): JSX.Element {
        return (
            <div className='section'>
                <Breadcrumbs className='bread-crumbs'>
                    <Link href='#/customer-care'>TICKETS</Link>
                    <div className='current-text'>CREATE A TICKET</div>
                </Breadcrumbs>

                <div className='header-1'>Create Ticket</div>

                <InputField 
                    placeholder='Search Member/Client ID'
                    value={this.state.searchText}
                    onChange={(value: string) => {
                        this.setState({searchText: value});
                    }} />

                <SearchedMembersList 
                    searchText={this.state.searchText}
                    onSelectMember={(cardNo: string) => {
                        this.getMemberData(cardNo);
                        this.props.onUpdateMemberStateChange(undefined);
                        this.setState({
                            searchText: '',
                            loadingMember: true,
                            recentTickets: []
                        });
                    }} />

                <TextButton 
                    icon={faSearch}
                    label='Advance Search'
                    onClick={() => {

                    }}
                    />
            </div>
        );
    }

    renderTopArea(): JSX.Element {
        return (
            <div className='section'>
                <Breadcrumbs className='bread-crumbs'>
                    <Link href='#/customer-care'>TICKETS</Link>
                    <Link className='current-text'>{this.props.ticketStatus.toUpperCase() + ' TICKET'}</Link>
                </Breadcrumbs>

                {this.props.ticket ? (
                    <span className='header-1'>
                        {`${this.props.ticketStatus} Ticket ${this.props.ticket.ticketNo}`}
                    </span>
                ) : null}
                

            </div>
        );
    }

    renderMemberInfo(member: MemberData): JSX.Element {
        if (!this.state.recentTickets) {
            this.getRecentTicketsData(member.cardNo);
        }

        const name =
            member.firstName + ' ' + 
            GlobalFunction.getIntials(member.middleName) + ' ' +
            member.lastName + ' '
        const birthDate = GlobalFunction.toShortDateString(new Date(member.birthDate));
        const age = GlobalFunction.getAge(new Date(member.birthDate));
        return (
            <div className='section'>
                <div className='circle' style={{backgroundColor: '#3AB77D'}} />
                <div style={{display: 'inline-block'}}>
                    <div className='header-2'>{member.cardNo}</div>
                </div>
                <div className='text'>{name}</div>
                <div className='text'>{member.clientName}</div>
                <div className='text'>{'---'}</div>
                <Grid container>
                    {/* <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>{"R&B"}</div>
                        <div className='small-text'>{"---"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>{"R&B Limit"}</div>
                        <div className='small-text'>{"---"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>MBL LIMIT</div>
                        <div className='small-text'>{"---"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>PEC LIMIT</div>
                        <div className='small-text'>{"---"}</div>
                    </Grid> */}
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>BIRTHDATE (AGE)</div>
                        <div className='small-text'>{birthDate + " (" + age + ")"}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>GENDER / STATUS</div>
                        <div className='small-text'>{member.gender + " / " + member.civilStatus}</div>
                    </Grid>
                </Grid>
                <div>
                    {this.state.recentTickets && this.state.recentTickets.length > 0 ? (
                        <>
                        <div className='header-2'>OPEN/PENDING TICKETS</div>
                        <CustomTable
                            paginationClass='custom-pagination-small'
                            className='custom-table-small'
                            labelRowsPerPage='Rows:'
                            rows={this.state.recentTickets}
                            rowsPerPageOptions={[5, 10, 25]}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: SimpleTicketData, index: number) =>
                                this.renderTableRow(rowData, index)} />
                        </>
                    ) : null}
                </div>
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            <TableCell style={{minWidth: '70px', fontSize: '10px'}}>Ticket ID</TableCell>
            <TableCell style={{fontSize: '10px'}}>Type</TableCell>
            <TableCell style={{minWidth: '90px', fontSize: '10px'}}>Last Update</TableCell>
            </>
        );
    }

    renderTableRow(recentTicket: SimpleTicketData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor, fontSize: '10px'}}>
                <TableCell>{recentTicket.ticketNo}</TableCell>
                <TableCell>{recentTicket.specificType}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(recentTicket.lastUpdate)}</TableCell>
            </TableRow>
        );
    }

    renderTicketData(ticket: TicketData): JSX.Element {
        return (
            <div className='section'>
                <div className='sub-section'>
                    <div className='light-text'>TICKET TYPE</div>
                    <div className='small-text'>{ticket.ticketType}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>SPECIFIC TYPE</div>
                    <div className='small-text'>{ticket.specificType}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>URGENCY</div>
                    <div className='small-text'>{ticket.urgency}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>ASSIGNED TO</div>
                    <div className='small-text'>{ticket.agent.name}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>RECEIVED VIA</div>
                    <div className='small-text'>{ticket.ticketSource}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>RECEIVED FROM</div>
                    <div className='small-text'>{ticket.callerName}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>NUMBER/EMAIL</div>
                    <div className='small-text'>{ticket.contactNo}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>CALL TRANSACTION NO.</div>
                    <div className='small-text'>{ticket.transactionNo}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>CREATED BY</div>
                    <div className='small-text'>{ticket.createdBy.name}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME CREATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticket.dateCreated)}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME LAST UPDATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticket.dateUpdated)}</div>
                </div>
            </div>
        );
    }
}