import * as React from 'react';
import { Button } from '@material-ui/core';

interface IProps {
    onUpdateTicket: () => void;
    onSaveForNow: () => void;
    onAssign: () => void;
    onEscalate: () => void;
    onResolve: () => void;
}

export class UpdateTicketFooter extends React.Component<IProps> {
    
    constructor(props: IProps) {
        super(props);
    }

    shouldComponentUpdate(_props: IProps, _state: any) {
        return false;
    }

    render(): JSX.Element {
        return (
            <div className='footer'>
                <Button className='button-transparent'>
                        Save For Now
                </Button>
                <Button 
                    className='button-neutral'
                    onClick={this.props.onAssign}>
                    Assign
                </Button>
                <Button 
                    className='button-error'
                    onClick={this.props.onEscalate}>
                    Escalate
                </Button>
                <Button 
                    className='button'
                    onClick={this.props.onUpdateTicket}>
                    Update Ticket
                </Button>
                <Button 
                    className='button'
                    onClick={this.props.onResolve}>
                    Resolve Ticket
                </Button>
                
            </div>
        );
    }
}