// This should be placed in the frontend/core
export class LoginPage extends React.Component {

	constructor(props: ILoginPage.IProps) {
		super(props);
	}

	onLogin = () => {
		alert("onLogin() clicked");
	}

	onForgotPassword = () => {
		alert("onForgotPassword() clicked");
	}

	onContact = () => {
		alert("onContact() clicked");
	}

	public render(): JSX.Element {
		return (
			<Grid container className='login-main'>
				<img className='login-shape-1' src={require('./images/shape-1.png')}/>
				<img className='login-shape-2' src={require('./images/shape-2.png')}/>
				<div style={{position: 'fixed'}}>
				{/* <Link href='/#/customer-care'>Main menu</Link> */}
				</div>
				<Grid item xs={6}></Grid>
				<Grid item xs={6} className="login-block">

					<img className='login-logo' src={require('./icons/logo/logo@2x.png')} />

					<div className='login-project-name'>Intellicare HIMS</div>
					<div className='login-input-area'>
						<span className='login-text'>Username</span>
						<InputBase 
							className='login-text-field'
							fullWidth 
							onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
								console.log(event);
							}}
						/>
					</div>
					
					<div className='login-input-area'>
						<Grid container className='login-text'>
							<Grid item xs={6}>Password</Grid>
							<Grid item xs={6} style={{textAlign: 'right'}}>
								<Link onClick={this.onForgotPassword}>Forgot Password</Link>
							</Grid>
						</Grid>
						<InputBase 
							className='login-text-field'
							fullWidth 
							type='password'
							onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
								console.log(event);
							}}
						/>
					</div>
					
					<Button className='login-button' onClick={this.onLogin}>
						LOG IN
					</Button>

					<div style={{paddingTop: '3rem'}}>
						Problems logging in? Please
						&nbsp;<Link onClick={this.onContact}>contact your IT Team</Link>&nbsp;
						for assistance.
					</div>
				</Grid>
			</Grid>
		);
	}
}

